@charset 'utf-8';

@import '~spectre.css/src/variables';
@import '~spectre.css/src/mixins';
@import '~spectre.css/src/normalize';
@import '~spectre.css/src/base';

@import '~spectre.css/src/layout';
@import '~spectre.css/src/off-canvas';
@import '~spectre.css/src/utilities';

*,
*::after,
*::before {
  box-sizing: border-box;
}

html {
  overflow-x: hidden;
}

html,
body {
  display: flex;
  height: 100vh;
  flex-direction: column;
  font-family: Montserrat, Arial, Helvetica, sans-serif;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

$navbar-backgroundcolor:#782B2A;
$navbar-height: 122px;

$content-backgroundcolor: #f4f4f4;

$flag-height: 20px;

$footer-backgroundcolor: #4a5149;

$flag-red: #EF5E5D;
$flag-white: #E3EAC1;
$flag-green: #7BAB5B;

$color-white: #ffffff;
$color-red: #EF5E5D;
$color-primary: #4a5149;
$color-green: #7aa95b;

header {
  min-height: $navbar-height;
  background-color: $navbar-backgroundcolor;

  .header-image {
    height: 64px;
    width: 153px;
  }

  .language-selector {
    font-size: 18px;
    font-weight: 300;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.46;
    letter-spacing: normal;
    color: $color-white;
  
    .selected {
      font-weight: 800;
    }
  }

  .menu-item {
    cursor: pointer;
    font-size: 18px;
    font-weight: 300;
    font-stretch: normal;
    font-style: normal;
    line-height: 2.11;
    letter-spacing: 0.45px;
    text-align: center;
    color: #ffffff;
    margin-right: 15px;

    @media screen and (max-width: $size-lg) {
      display: none;
    }

    &.selected {
      color: $color-green;
      font-weight: 800;
    }

    &:hover {
      color: $color-green;
    }
  }

}

.banner {
  position: relative;

  .banner-image {
    width: 100%;
  }

  .banner-text {
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
    top: 5px;

    @media screen and (min-width: $size-xs) {
      top: 10px;
    }
    @media screen and (min-width: $size-sm) {
      top: 17px;
    }
    @media screen and (min-width: $size-md) {
      top: 24px;
    }
    @media screen and (min-width: $size-lg) {
      top: 31px;
    }
    @media screen and (min-width: $size-xl) {
      top: 38px;
    }
    @media screen and (min-width: $size-2x) {
      top: 45px;
    }

    .first-row-container {
      margin-bottom: 18px;

      .first-row-part1 {
        font-weight: 900;
        line-height: 0.53;
        letter-spacing: 0.99px;
        color: $color-white;
        font-size: 24px;

        @media screen and (min-width: $size-xs) {
          font-size: 24px;
        }
        @media screen and (min-width: $size-sm) {
          font-size: 28px;
        }
        @media screen and (min-width: $size-md) {
          font-size: 32px;
        }
        @media screen and (min-width: $size-lg) {
          font-size: 36px;
        }
        @media screen and (min-width: $size-xl) {
          font-size: 40px;
        }
        @media screen and (min-width: $size-2x) {
          font-size: 40px;
        }
      }
  
      .first-row-part2 {
        font-stretch: normal;
        font-style: normal;
        letter-spacing: 0.99px;
        color: $color-white;
        line-height: 0.48;
        font-size: 24px;

        @media screen and (min-width: $size-xs) {
          font-size: 24px;
        }
        @media screen and (min-width: $size-sm) {
          font-size: 28px;
        }
        @media screen and (min-width: $size-md) {
          font-size: 32px;
        }
        @media screen and (min-width: $size-lg) {
          font-size: 36px;
        }
        @media screen and (min-width: $size-xl) {
          font-size: 40px;
        }
        @media screen and (min-width: $size-2x) {
          font-size: 40px;
        }
      }
    }

    .second-row {
      font-stretch: normal;
      font-style: normal;
      letter-spacing: 0.99px;
      color: $color-white;
      line-height: 0.48;
      margin-bottom: 18px;
      letter-spacing: 3;
      font-size: 24px;

      @media screen and (min-width: $size-xs) {
        font-size: 24px;
      }
      @media screen and (min-width: $size-sm) {
        font-size: 28px;
      }
      @media screen and (min-width: $size-md) {
        font-size: 32px;
      }
      @media screen and (min-width: $size-lg) {
        font-size: 36px;
      }
      @media screen and (min-width: $size-xl) {
        font-size: 40px;
      }
      @media screen and (min-width: $size-2x) {
        font-size: 40px;
      }
    }

    .content-divider-container {
      .content-divider {
        background-color: black;
        margin-top: 18px;
        margin-bottom: 18px;
        height: 2px;
        width: 180px;

        @media screen and (min-width: $size-xs) {
          height: 2px;
          width: 245px;
        }
        @media screen and (min-width: $size-sm) {
          height: 2px;
          width: 270px;
        }
        @media screen and (min-width: $size-md) {
          height: 3px;
          width: 290px;
        }
        @media screen and (min-width: $size-lg) {
          height: 3px;
          width: 315px;
        }
        @media screen and (min-width: $size-xl) {
          height: 4px;
          width: 330px;
        }
        @media screen and (min-width: $size-2x) {
          height: 4px;
          width: 340px;
        }
      }
    }

    .third-row {
      font-weight: 900;
      font-stretch: normal;
      font-style: normal;
      line-height: 0.63;
      letter-spacing: 3.71px;
      color: $color-white;
      font-size: 37px;

      @media screen and (min-width: $size-xs) {
        font-size: 37px;
      }
      @media screen and (min-width: $size-sm) {
        font-size: 41px;
      }
      @media screen and (min-width: $size-md) {
        font-size: 45px;
      }
      @media screen and (min-width: $size-lg) {
        font-size: 49px;
      }
      @media screen and (min-width: $size-xl) {
        font-size: 53px;
      }
      @media screen and (min-width: $size-2x) {
        font-size: 53px;
      }
    }
  }
}

.content {
  background-color: $content-backgroundcolor;

  .content-title {
    margin-top: 80px;
    margin-bottom: 80px;
    font-weight: 800;
    font-stretch: normal;
    font-style: normal;
    line-height: 0.71;
    letter-spacing: normal;
    color: $color-red;
    font-size: 30px;

    @media screen and (min-width: $size-xs) {
      font-size: 30px;
    }
    @media screen and (min-width: $size-sm) {
      font-size: 34px;
    }
    @media screen and (min-width: $size-md) {
      font-size: 38px;
    }
    @media screen and (min-width: $size-lg) {
      font-size: 42px;
    }
    @media screen and (min-width: $size-xl) {
      font-size: 46px;
    }
    @media screen and (min-width: $size-2x) {
      font-size: 50px;
    }
  }

  .content-description {
    font-weight: 300;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.46;
    letter-spacing: normal;
    color: $color-primary;
    margin-bottom: 18px;
    font-size: 19px;

    @media screen and (min-width: $size-xs) {
      font-size: 19px;
    }
    @media screen and (min-width: $size-sm) {
      font-size: 20px;
    }
    @media screen and (min-width: $size-md) {
      font-size: 21px;
    }
    @media screen and (min-width: $size-lg) {
      font-size: 22px;
    }
    @media screen and (min-width: $size-xl) {
      font-size: 23px;
    }
    @media screen and (min-width: $size-2x) {
      font-size: 24px;
    }

    &:last-child {
      margin-bottom: 110px;
    }
  }

  .content-divider-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;

    .content-divider {
      width: 200px;
      height: 2px;
      background-color: $color-green;
      margin-top: 40px;
      margin-bottom: 40px;
    }
  }
}

.logos {
  background-color: #ffffff;

  .logos-item {
    margin-top: 114px;
    margin-bottom: 114px;
    align-self: flex-end;
  }

  .logos-container {
    display: flex;
    justify-content: space-between;
  }

  @media screen and (max-width: $size-md) {
    .logos-item {
      margin-top: 57px;
      margin-bottom: 57px;
      align-self: center;
    }

    .logos-container {
      flex-direction: column;
    }
  }
}

.flag {
  min-height: $flag-height;
  display: flex;

  .red {
    width: 28%;
    background-color: $flag-red;
  }

  .white {
    width: 23%;
    background-color: $flag-white;
  }

  .green {
    width: 49%;
    background-color: $flag-green;
  }
}

footer {
  background-color: $footer-backgroundcolor;

  .footer-logo {
    margin-top: 60px;
    margin-bottom: 60px;
  }

  .footer-text-container {
    margin-bottom: 114px;

    .footer-text {
      margin-bottom: 114px;
      font-weight: 300;
      font-stretch: normal;
      font-style: normal;
      line-height: 2.22;
      letter-spacing: normal;  
      color: $color-white;
      font-size: 14px;

      @media screen and (min-width: $size-xs) {
        font-size: 14px;
      }
      @media screen and (min-width: $size-sm) {
        font-size: 15px;
      }
      @media screen and (min-width: $size-md) {
        font-size: 15px;
      }
      @media screen and (min-width: $size-lg) {
        font-size: 16px;
      }
      @media screen and (min-width: $size-xl) {
        font-size: 17px;
      }
      @media screen and (min-width: $size-2x) {
        font-size: 18px;
      }
    }
  }
}

.color-white {
  color:$color-white;
}

.color-red  {
  color: $color-red;
}

.color-primary {
  color: $color-primary;
}

.vertically-centerize {
  display: flex;
  align-items: center;
}

#hu, #en {
  cursor: pointer;
}

.menu-icon {
  color: white;
  font-size: 40px;
}

.off-canvas-toggle {
  @media screen and (min-width: $size-lg) {
    display: none;
  }
}


.off-canvas-sidebar {
   background-color: $navbar-backgroundcolor !important;
   max-width: 300px !important;

   .menu-text {
    font-size: 32px;
    font-weight: 700;
    margin-top: 30px;
    color: white;
  }

   .menu-item {
    font-size: 20px;
    font-weight: 700;
    margin-top: 30px;
    color: white;

    &.selected {
      color: $color-green;
      font-weight: 800;
    }

    &:hover {
      color: $color-green;
    }
  }
}

.logo-menu-container {
  display: flex;

  .off-canvas-toggle {
    margin-right: 10px;
  }
}

.mt-20 {
  margin-top: 20px;
}

